<template>
  <div>
    <div id="fomr1-container">
        <el-form :inline="true" :model="form1">
            <div v-if="others" class = 'others'>
                <el-form-item label="客户名称">
                    <el-select v-model="name" placeholder="请选择" @change="selectWay" filterable>
                    <el-option
                    v-for="item in centerInfo"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                    </el-option>
                    </el-select>
                </el-form-item>
            </div>
            
            <div v-else class = 'others'>
                <el-form-item label="客户名称">
                    <el-input v-model="name" placeholder="客户名称" clearable></el-input>
                </el-form-item>
            </div>

            <el-form-item label="中心点(A端)地址">
                <el-input v-model="form1.Asite" placeholder="A端地址" clearable></el-input>
            </el-form-item>

            <el-form-item label="A端对应分局">
                <el-input v-model="form1.Apart" placeholder="A端局向" clearable></el-input>
            </el-form-item>

            <el-form-item label="A端机房">
                <el-input v-model="form1.Aroom" placeholder="A端机房" clearable></el-input>
            </el-form-item>
        </el-form>
    </div>

    <div id="fomr2-container">
        <el-form :inline="true" :model="form2">
            <el-form-item label="分支点(Z端)地址">
                <el-input v-model="form2.Zsite" placeholder="Z端地址" clearable></el-input>
            </el-form-item>

            <el-form-item label="Z端对应分局">
                <el-input v-model="form2.Zpart" placeholder="Z端局向" clearable></el-input>
            </el-form-item>

            <el-form-item label="Z端机房">
                <el-input v-model="form2.Zroom" placeholder="Z端机房" clearable></el-input>
            </el-form-item>
        </el-form>
    </div>

    <div id="fomr3-container">
        <el-form :inline="true" :model="form3" class="demo-form-inline">
            <el-form-item label="带宽速率">
                <el-input v-model="form3.netSpeed" placeholder="速率" clearable></el-input>
            </el-form-item>
            
            <el-form-item label="用户联系人">
                <el-input v-model="form3.user" placeholder="用户联系人" clearable></el-input>
            </el-form-item>

            <el-form-item label="客户经理">
                <el-input v-model="form3.manager" placeholder="客户经理" clearable></el-input>
            </el-form-item>
        </el-form>
    </div>

    <div id="fomr4-container">
        <el-form :inline="true" :model="form4" class="demo-form-inline">
            <el-form-item label="光路接入号">
                <el-input v-model="form4.lightPathNum" placeholder="光路接入号" clearable></el-input>
            </el-form-item>

            <el-form-item label="光路名称">
                <el-input v-model="form4.lightPathName" placeholder="光路名称" clearable></el-input>
            </el-form-item>

            <el-form-item label="光路路由">
                <el-input v-model="form4.lightPathWay" placeholder="光路路由" clearable></el-input>
            </el-form-item>
        </el-form>
    </div>

    <div id="form5-container">
        <el-form :inline="true" :model="form5" class="demo-form-inline">
            <el-form-item label="电路接入号">
                <el-input v-model="form5.circuitNum" placeholder="电路接入号" clearable></el-input>
            </el-form-item>

            <el-form-item label="电路名称">
                <el-input v-model="form5.circuitName" placeholder="电路名称" clearable></el-input>
            </el-form-item>

            <el-form-item label="电路路由">
                <el-input v-model="form5.circuitWay" placeholder="电路路由" clearable></el-input>
            </el-form-item>

            <el-form-item label="分支点新建设备类型">
                <el-select v-model="machineType" placeholder="请选择">
                    <el-option
                    v-for="item in form5.WaySelect"
                    :key="item.value"
                    :label="item.label"
                    :value="item.label">
                    </el-option>
                </el-select>
            </el-form-item>

            <el-form-item label="A端是否汇聚">
                <el-switch
                v-model="form5.jugement"
                active-color="#13ce66"
                inactive-color="#ff4949">
                </el-switch>
            </el-form-item>
        </el-form>
    </div>

    <div v-if="form5.jugement" id="form6-container">
        <el-form :inline="true" :model="form6" class="demo-form-inline">
            <el-form-item label="中心点网管标识">
                <el-input v-model="form6.identification" placeholder="中心点网管标识" clearable></el-input>
            </el-form-item>

            <el-form-item label="设备端口">
                <el-input v-model="form6.port" placeholder="设备端口" clearable></el-input>
            </el-form-item>

            <el-form-item label="业务VLAN">
                <el-input v-model="form6.vlan" placeholder="业务VLAN" clearable></el-input>
            </el-form-item>
        </el-form>
    </div>

    <el-form :inline="true" class="demo-form-inline">
        <el-form-item label="调单编号">
            <el-input v-model="labelNum" placeholder="调单编号" clearable></el-input>
        </el-form-item>
        <el-button type="primary" @click="generateNum">生成调单号</el-button>
        <el-button type="success" round @click="downBtn">导出</el-button>
    </el-form>
  </div>
</template>

<script>
export default {
    created(){
        this.$axios.post("http://localhost:8888/centerInfoSelect").then(
            res=>{
                console.log(res.data)
                this.centerInfo = res.data.map(item=>{
                    return {value:item[1],label:item[1], site:item[2], regionPart:item[3], manager:item[4]}
                })
                const otherData = {value:this.centerInfo.length+1, label:"其他"}
                this.centerInfo = [...this.centerInfo,otherData]
            }
        ).catch(err=>console.log(err))

        this.form6.dispatchNum = this.$storeNew.get("dispatchNum")

    },




    data(){
        return{
            name:'',
            centerInfo: [],
            others:true,
            dispatchMan:'',
            phone:'',
            form1:{
                Asite:'',
                Apart:'',
                Aroom:''
            },
            form2:{
                Zsite:'',
                Zpart:'',
                Zroom:''
            },
            form3:{
                netSpeed:'',
                user:'',
                manager:''
            },
            form4:{
                lightPathNum:'',
                lightPathName:'',
                lightPathWay:''
            },
            form5:{
                circuitNum:'',
                circuitName:'',
                circuitWay:'',
                WaySelect:[
                    {value:1,label:'新建光收发器'},
                    {value:2,label:'新建MSAP多以太'}
                ],
                jugement:true
            },
            form6:{
                identification:'',
                port:'',
                vlan:'',
                dispatchNum:'' 
            },
            machineType:''
        }
    },

    computed:{
        labelNum(){
            const d = new Date()
            return 'ZYBD'+d.getFullYear()+'-'+(d.getMonth()+1)+'-'+this.form6.dispatchNum            
        }
    },



    methods:{
        selectWay(index){
            if(index !== this.centerInfo.length){
                const newArr = this.centerInfo.filter(item=>index == item.value)
                this.form1.Asite = newArr[0].site
                this.form1.Apart = newArr[0].regionPart
                this.form3.manager = newArr[0].manager
            }else{
                this.name = ''
                this.form1.Asite=''
                this.form1.Apart=''
                this.form3.manager=''
                this.others = !this.others
            }
        },

        generateNum(){
        this.$axios.post('http://localhost:8888/update',
          {
            dispatchNum:this.form6.dispatchNum,
            username:this.$storeNew.get("username")
          }
        ).then(res=>{
          this.form6.dispatchNum = res.data.dispatchNum
          this.$storeNew.set("dispatchNum",this.form6.dispatchNum)
        }).catch(err=>console.log(err))
      },

        downBtn(){
            if(this.form5.jugement==false){
                this.form6.identification = '非汇聚'
                this.form6.port='无'
                this.form6.vlan='无'
            }


            this.$excel({
                filename:this.labelNum,
                sheet:[
                    {
                        sheetName:this.labelNum,
                        keys:['col1','col2'],
                        table:[
                            {col1:"电路（线路）调度、局数据修改通知单（机密）",col2:""},
                            {col1:"客户名称",col2:this.name},
                            {col1:"用户联系人",col2:this.form3.user},
                            {col1:"客户经理",col2:this.form3.manager},
                            {col1:"带宽速率",col2:this.form3.netSpeed},
                            {col1:"中心点(A端)地址",col2:this.form1.Asite},
                            {col1:"分支点(Z端)地址",col2:this.form2.Zsite},
                            {col1:"对应局向",col2:"A端:"+this.form1.Apart+",Z端:"+this.form2.Zpart},
                            {col1:"光路信息",col2:""},
                            {col1:"光路接入号",col2:this.form4.lightPathNum},
                            {col1:"光路名称",col2:this.form4.lightPathName},
                            {col1:"光路路由",col2:this.form4.lightPathWay},
                            {col1:"电路信息",col2:""},
                            {col1:"业务接入号",col2:this.form5.circuitNum},
                            {col1:"电路名称",col2:this.form5.circuitName},
                            {col1:"电路路由",col2:this.form5.circuitWay},
                            {col1:"新建设备类型",col2:this.machineType},
                            {col1:"汇聚口信息",col2:this.form6.identification+"  "+this.form6.port+"  VLAN:"+this.form6.vlan},
                            {col1:"调单联系人",col2:this.dispatchMan+this.phone}
                        ],
                        merges:["1-1:2-1","1-9:2-9","1-13:2-13",],
                        colWidth:[30,100],
                        globalStyle:{
                            font:{
                                name:"黑体"
                            },
                            alignment:{
                                wrapText:true
                            }
                        },
                        cellStyle:[
                            {
                                cell:"1-1",
                                fill:{
                                    fgColor:{rgb:"ffff33"}
                                },
                                font:{
                                    sz:14
                                }
                            },
                            {
                                cell:"1-9",
                                fill:{
                                    fgColor:{rgb:"ffff33"}
                                },
                                font:{
                                    sz:14
                                }
                            },
                            {
                                cell:"1-13",
                                fill:{
                                    fgColor:{rgb:"ffff33"}
                                },
                                font:{
                                    sz:14
                                }
                            }
                        ]
                    }
                ]
            })
        }

    }
}
</script>

<style>
    #fomr1-container,#fomr2-container,#fomr3-container,#fomr4-container,#form5-container,#form6-container{
        padding:20px
    }
    .others{
        padding-bottom: 40px;
    }
</style>