              <el-submenu index="2">
                  <template slot="title">
                    <i class="el-icon-menu"></i>
                    <span>H中继扩容</span>
                  </template>
                  <el-menu-item-group>
                    <el-menu-item index="2-1">单条扩容申请</el-menu-item>
                    <el-menu-item index="2-2">批量扩容申请</el-menu-item>
                  </el-menu-item-group>
