<template>
<div class="left">
  <el-form ref="form" :model="form" label-width="auto">
    <el-form-item label="接入号">
        <el-input v-model="form.num" ></el-input>
    </el-form-item>
    <el-form-item label="对应分局">
        <el-input v-model="form.region" ></el-input>
    </el-form-item>
    <el-form-item label="扩容点位名称">
        <el-input v-model="form.Hname" ></el-input>
    </el-form-item>
    <el-form-item label="OLT网管标识及端口">
        <el-input v-model="form.olt" ></el-input>
    </el-form-item>
    <el-form-item label="分光器网管标识">
        <el-input v-model="form.splitter" ></el-input>
    </el-form-item>
    <el-form-item label="分光比">
        <el-input v-model="form.splitterRate" ></el-input>
    </el-form-item>
    <el-form-item label="光路路由">
        <el-input v-model="form.lightPath" ></el-input>
    </el-form-item>
    <el-form-item label="监理公司联系人">
        <el-input v-model="form.supervision" ></el-input>
    </el-form-item>
    <el-form-item label="光缆施工联系人">
        <el-input v-model="form.construction" ></el-input>
    </el-form-item>
    <el-form-item label="调单号">
        <el-input v-model="labelNum"></el-input>
    </el-form-item>
    <el-form-item label="调单联系人">
      <el-select v-model="dispatchName" placeholder="请选择">
        <el-option
          v-for="item in options"
          :key="item.value"
          :label="item.label"
          :value="item.label">
        </el-option>
      </el-select>
    </el-form-item>
    <el-form-item>
            <el-button type="primary" @click="generateNum">生成调单号</el-button>
            <el-button @click = "reset">重置</el-button>
    </el-form-item>
    <el-form-item>
            <el-button type="success" @click="downFile">导出</el-button>
    </el-form-item>
  </el-form>
</div>
</template>

<script>

export default {
    created(){
      this.$axios.post("http://localhost:8888/users",{
        username:this.$storeNew.get("username"),
        password:this.$storeNew.get("password")
      }).then((res)=>{
        this.dispatchNum = res.data[0][3]
      }).catch((err)=>console.log(err))
    },

    data(){
        return{
          dispatchNum:'',

          form:{
              num:'',
              region:'',
              Hname:'',
              olt:'',
              splitter:'',
              splitterRate:'',
              lightPath:'',
              supervision:'',
              construction:'',
              name:''
            },
            options:[
              {
                value:1,
                label:"岳靖川19980798836"
              },
              {
                value:2,
                label:"聂嘉聃198XXXXX"
              },
              {
                value:3,
                label:"杨蜜19980401202"
              }
            ],
            dispatchName:''
        }
    },
    computed:{
        labelNum(){
            const d = new Date()
            return 'ZYBD'+d.getFullYear()+'-'+(d.getMonth()+1)+'-'+this.dispatchNum
        }
    },

    methods: {
        generateNum(){
        this.dispatchNum++,
        this.$axios.post('http://localhost:8888/update',
          {
            dispatchNum:this.dispatchNum,
            username:this.$storeNew.get("username")
          }
        ).then((res)=>console.log(res.data)).catch((err)=>console.log(err))
      },
      
      reset(){
        for(let key in this.form){
          this.form[key]=''
        }
    },

    downFile(){
      this.$excel({
        filename:this.labelNum,
        sheet:[
          {
            sheetName:this.labelNum,
            keys:['sheet1','sheet2'],
  
            table:[
              {sheet1:"调度依据",sheet2:this.form.num},
              {sheet1:this.form.region+'------'+this.form.Hname+"("+this.form.splitterRate+")",sheet2:''},
              {sheet1:"光路名称",sheet2:"GEPON工程(FTTH)("+this.form.num+")"+this.form.region+"/"+this.form.Hname+"001(1芯)"},
              {sheet1:'光路路由',sheet2:this.form.lightPath},
              {sheet1:this.form.region+"("+this.form.olt+")"+"------"+this.form.splitter+" 互联",sheet2:''},
              {sheet1:"联调分局",sheet2:this.form.region.substring(0,2)},
              {sheet1:"监理公司联系人",sheet2:this.form.supervision},
              {sheet1:"光缆施工联系人",sheet2:this.form.construction},
              {sheet1:"调单联系人",sheet2:this.dispatchName}
            ],

            merges:[
              "1-2:2-2",
              "1-5:2-5"
            ],

            colWidth:[
              30,100
            ],

            globalStyle:{
              font:{
                name:"黑体"
              },
              alignment:{
                wrapText:true
              }
            },
            cellStyle:[
            {
              cell:'1-2',
              fill:{
                fgColor:{ rgb:"ffff33"}
              },
              font:{
                size:14
              }
            },
            {
              cell:'1-5',
              fill:{
                fgColor:{ rgb:"ffff33"}
              },
              font:{
                size:14
              }
            }
            ]
          }
        ]
      })
    }

    }
}
</script>

<style>
.left{
    float: left;
}
</style>