<template>
<div>
  <div class="left">
    <el-form ref="form" :model="form" label-width="auto">
        <el-form-item label="接入号">
            <el-input v-model="form.num" ></el-input>
        </el-form-item>
        <el-form-item label="用户名称">
          <el-input v-model="form.username"></el-input>
        </el-form-item>
        <el-form-item label="新装地址">
          <el-input v-model="form.siteNew"></el-input>
        </el-form-item>
        <el-form-item label="带宽速率">
          <el-input v-model="form.netSpeed" ></el-input>
        </el-form-item>
        <el-form-item label="用户联系人">
          <el-input v-model="form.contactPerson" ></el-input>
        </el-form-item>
        <el-form-item label="客户经理">
          <el-input v-model="form.manager"></el-input>
        </el-form-item>
        <el-form-item label="光路名称">
          <el-input v-model="form.cableName"></el-input>
        </el-form-item>
        <el-form-item label="光路路由">
          <el-input v-model="form.cablePath"></el-input>
        </el-form-item>
        <el-form-item label="光缆段编码">
          <el-input v-model="form.cableCode"></el-input>
        </el-form-item>
        <el-form-item label="对应分局">
          <el-input v-model="form.regions"></el-input>
        </el-form-item>
        <el-form-item label="调单号">
          <el-input v-model="labelNum"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="success" round @click="generateNum">生成调单号</el-button>
        </el-form-item>
        <el-form-item label="是否直连MSE">
            <el-switch v-model="delivery"></el-switch>
        </el-form-item>
        <el-form-item label="调单联系人">
        <el-select v-model="dispatchName" placeholder="请选择">
          <el-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.label">
          </el-option>
        </el-select>
      </el-form-item>
        <el-form-item>
            <el-button type="primary" @click="downFile">导出</el-button>
            <el-button @click = "reset">重置</el-button>
        </el-form-item>
    </el-form>
  </div>
  <div class="right">
    <div v-if="!delivery">
      <el-form ref="form" :model="form2" label-width="auto">
        <el-form-item label="数据端口">
          <el-input v-model="form2.dataPort"></el-input>
        </el-form-item>
        <el-form-item label="ONU端口">
          <el-input v-model="onuPort"></el-input>
        </el-form-item>
        <el-form-item label="ONUID">
          <el-input v-model="form2.onuId" ></el-input>
        </el-form-item>
        <el-form-item label="Vlan">
          <el-input v-model="form2.vlanNum"></el-input>
        </el-form-item>
        <el-form-item label="OLT端口">
          <el-input v-model="form2.oltPort"></el-input>
        </el-form-item>
      </el-form>
    </div>
    <div v-else-if="delivery">
      <el-form  :model="form2" label-width="auto">
        <el-form-item label="数据端口">
          <el-input v-model="form2.dataPort"></el-input>
        </el-form-item>
      </el-form>
    </div>
  </div>
</div>
</template>


<script>
  export default {
    created(){
      this.$axios.post("http://localhost:8888/users",{
        username:this.$storeNew.get("username"),
        password:this.$storeNew.get("password")
      }).then((res)=>{
        this.dispatchNum = res.data[0][3]
      }).catch((err)=>console.log(err))
    },

    data() {
      return {
        form: {
          num: '',
          username: '',
          siteNew:'',
          netSpeed:'',
          contactPerson:'',
          manager:'',
          cableName:'',
          cablePath:'',
          cableCode:'',
          regions:''
        },
        
        dispatchNum:'',

        delivery: false,

        form2:{
          dataPort:'',
          onuId:'',
          vlanNum:'',
          oltPort:''
        },

        options:[
              {
                value:1,
                label:"岳靖川19980798836"
              },
              {
                value:2,
                label:"徐桂英199XXXXXX"
              }
          ],
          dispatchName:''
      }
    },

    computed:{
      onuPort(){
        return this.form2.dataPort+'-1'
      },
      labelNum(){
        const d = new Date()
        return 'ZYBD'+d.getFullYear()+'-'+(d.getMonth()+1)+'-'+this.dispatchNum
      }
    },

    methods: {
      reset(){
          for(let key in this.form){
            this.form[key]=''
          }
          for(let key2 in this.form2){
            this.form2[key2]=''
          }
          this.delivery=false
      },

      generateNum(){
        this.dispatchNum++
        this.$axios.post('http://localhost:8888/update',
          {
            dispatchNum:this.dispatchNum,
            username:this.$storeNew.get("username")
          }
        ).then((res)=>console.log(res.data)).catch((err)=>console.log(err))
      },

      downFile(){
        if(!this.delivery){
          this.$excel({
          filename:this.labelNum,
          sheet:[
            {
              sheetName:this.labelNum,
              keys:['sheet1','sheet2'],
              table:[
                {sheet1:"传统商务光纤 新开,请按调单进行线路调通",sheet2:''},
                {sheet1:"调度依据",sheet2:this.form.num},
                {sheet1:"新装地址",sheet2:this.form.siteNew},
                {sheet1:"光路信息",sheet2:""},
                {sheet1:"光路名称",sheet2:this.form.cableName},
                {sheet1:"光路路由",sheet2:this.form.cablePath},
                {sheet1:"光路路由",sheet2:this.form.cableCode},
                {sheet1:"相关设备及数据信息",sheet2:""},
                {sheet1:"数据端口",sheet2:this.form2.dataPort},
                {sheet1:"ONU端口",sheet2:this.onuPort},
                {sheet1:"ONUID:"+this.form2.onuId,sheet2:"VLAN:"+this.form2.vlanNum},
                {sheet1:"速率"+this.form.netSpeed,sheet2:"OLT端口"+this.form2.oltPort},
                {sheet1:"其他信息",sheet2:""},
                {sheet1:"调测分局",sheet2:this.form.regions},
                {sheet1:"用户联系人",sheet2:this.form.contactPerson},
                {sheet1:"客户经理",sheet2:this.form.manager},
                {sheet1:"调单联系人",sheet2:this.dispatchName}
              ],
              merges:[
                "1-1:2-1","1-4:2-4","1-8:2-8","1-13:2-13"
              ],

              colWidth:[50,150],
              globalStyle:{
                font:{name:"黑体"},
                alignment:{wrapText:true}
              },
              cellStyle:[
                {cell:'1-1',fill:{fgColor:{rgb: "ffff33"}},font:{sz:14,bold:true}},
                {cell:'1-4',fill:{fgColor:{rgb: "ffff33"}},font:{sz:14,bold:true}},
                {cell:'1-8',fill:{fgColor:{rgb: "ffff33"}},font:{sz:14,bold:true}},
                {cell:'1-13',fill:{fgColor:{rgb: "ffff33"}},font:{sz:14,bold:true}}
              ]
            }
          ]
        })
        }
        else{
          this.$excel({
          filename:this.labelNum,
          sheet:[
            {
              sheetName:this.labelNum,
              keys:['sheet1','sheet2'],
              table:[
                {sheet1:"传统商务光纤 新开,请按调单进行线路调通",sheet2:''},
                {sheet1:"调度依据",sheet2:this.form.num},
                {sheet1:"新装地址",sheet2:this.form.siteNew},
                {sheet1:"光路信息",sheet2:""},
                {sheet1:"光路名称",sheet2:this.form.cableName},
                {sheet1:"光路路由",sheet2:this.form.cablePath},
                {sheet1:"光路路由",sheet2:this.form.cableCode},
                {sheet1:"相关设备及数据信息",sheet2:""},
                {sheet1:"数据端口",sheet2:this.form2.dataPort},
                {sheet1:"速率",sheet2:this.form.netSpeed},
                {sheet1:"其他信息",sheet2:""},
                {sheet1:"调测分局",sheet2:this.form.regions},
                {sheet1:"用户联系人",sheet2:this.form.contactPerson},
                {sheet1:"客户经理",sheet2:this.form.manager},
                {sheet1:"调单联系人",sheet2:this.dispatchName}
              ],
              merges:[
                "1-1:2-1","1-4:2-4","1-8:2-8","1-11:2-11"
              ],

              colWidth:[50,150],
              globalStyle:{
                font:{name:"黑体"},
                alignment:{wrapText:true}
              },
              cellStyle:[
                {cell:'1-1',fill:{fgColor:{rgb: "ffff33"}},font:{sz:14,bold:true}},
                {cell:'1-4',fill:{fgColor:{rgb: "ffff33"}},font:{sz:14,bold:true}},
                {cell:'1-8',fill:{fgColor:{rgb: "ffff33"}},font:{sz:14,bold:true}},
                {cell:'1-11',fill:{fgColor:{rgb: "ffff33"}},font:{sz:14,bold:true}}
              ]
            }
          ]
        })
        }
      }
    
    }
  }
</script>


<style scoped>
  .left {
      float: left;
  }

  .right {
      float: right;
      padding-right: 50%;
  }
</style>