import Vue from "vue"
import VueRouter from "vue-router"

import FiberNew from "@/components/FiberNew.vue"
import Home from "@/components/home/Home.vue"
import Login from "@/components/login/Login.vue"
import Hadd from '@/components/Hadd.vue'
import MSTPnew from '@/components/MSTPnew.vue'
import BatchMstpRemove from '@/components/BatchMstpRemove.vue'
import BatchHadd from '@/components/BatchHadd.vue'

Vue.use(VueRouter)


const router = new VueRouter({
    routes:[
        {path:"/",redirect:"/login"},
        {path:"/login",component:Login},
        {path:"/home", component:Home,children:[
            {path:"fiberNew",component:FiberNew},
            {path:"Hadd",component:Hadd},
            {path:'MSTPnew',component:MSTPnew},
            {path:"BatchMstpRemove",component:BatchMstpRemove},
            {path:"BatchHadd",component:BatchHadd}
        ]}
    ]
})

