<template>
<div>
  <el-form :inline="true">
    <el-form-item label="接入号">
        <el-input v-model="form.num" ></el-input>
    </el-form-item>
    <el-form-item label="对应分局">
        <el-input v-model="form.region" ></el-input>
    </el-form-item>
    <el-form-item label="扩容点位名称">
        <el-input v-model="form.Hname" ></el-input>
    </el-form-item>
    <el-form-item label="OLT网管标识及端口">
        <el-input v-model="form.olt" ></el-input>
    </el-form-item>
    <el-form-item label="分光器网管标识">
        <el-input v-model="form.splitter" ></el-input>
    </el-form-item>
    <el-form-item label="分光比">
        <el-input v-model="form.splitterRate" ></el-input>
    </el-form-item>
    <el-form-item label="光路路由">
        <el-input v-model="form.lightPath" ></el-input>
    </el-form-item>
    <el-form-item label="监理公司联系人">
        <el-input v-model="form.supervision" ></el-input>
    </el-form-item>
    <el-form-item label="光缆施工联系人">
        <el-input v-model="form.construction" ></el-input>
    </el-form-item>
    <el-form-item label="调单号">
        <el-input v-model="labelNum"></el-input>
    </el-form-item>
    <el-form-item>
        <el-button type="primary" @click="generateNum">生成调单号</el-button>
        <el-button @click = "addData">添加</el-button>
    </el-form-item>
    <el-form-item>
        <el-button type="success" @click="downFile">导出</el-button>
    </el-form-item>
  </el-form>

    <el-table
      :data="tableData"
      style="width: 100%">
      <el-table-column
        prop="num"
        label="接入号"
        width="180">
      </el-table-column>
      <el-table-column
        prop="region"
        label="对应分局"
        width="80">
      </el-table-column>
      <el-table-column
        prop="Hname"
        label="扩容点位名称"
        width="200">
      </el-table-column>
      <el-table-column
        prop="olt"
        label="OLT网管标识及端口"
        width="180">
      </el-table-column>
      <el-table-column
        prop="splitter"
        label="分光器网管标识"
        width="180">
      </el-table-column>
      <el-table-column
        prop="splitterRate"
        label="分光比"
        width="150">
      </el-table-column>
      <el-table-column
        prop="lightPath"
        label="光路路由"
        width="250">
      </el-table-column>
      <el-table-column
        prop="supervision"
        label="监理公司联系人"
        width="160">
      </el-table-column>
      <el-table-column
        prop="construction"
        label="光缆施工联系人"
        width="160">
      </el-table-column>
    </el-table>

</div>
</template>

<script>
export default {
    created(){
      this.$axios.post("http://localhost:8888/users",{
        username:this.$storeNew.get("username"),
        password:this.$storeNew.get("password")
      }).then((res)=>{
        this.dispatchNum = res.data[0][3]
        this.dispatchMan = res.data[0][4]
        this.phone = res.data[0][5]
      }).catch((err)=>console.log(err))
    },

    data(){
        return{
          dispatchNum:'',
          form:{
              num:'',
              region:'',
              Hname:'',
              olt:'',
              splitter:'',
              splitterRate:'',
              lightPath:'',
              supervision:'',
              construction:'',
            },
          dispatchMan:'',
          phone:'',
          tableData: [],
          newTable: [],
          mergesData:[],
          cellStyleData:[]
        }
    },
    computed:{
        labelNum(){
            const d = new Date()
            return 'ZYBD'+d.getFullYear()+'-'+(d.getMonth()+1)+'-'+this.dispatchNum
        }
    },

    methods:{
      generateNum(){
        this.dispatchNum++
        this.$axios.post('http://localhost:8888/update',
          {
            dispatchNum:this.dispatchNum,
            username:this.$storeNew.get("username")
          }
        ).catch((err)=>console.log(err))
      },
      
      addData(){
        this.tableData.push({
          num:this.form.num,
          region:this.form.region,
          Hname:this.form.Hname,
          olt:this.form.olt,
          splitter:this.form.splitter,
          splitterRate:this.form.splitterRate,
          lightPath:this.form.lightPath,
          supervision:this.form.supervision,
          construction:this.form.construction
        })

        for(let key in this.form){
          this.form[key] = ''
        }

    },

    downFile(){
      const tableLength = this.tableData.length

      for(let i = 0; i <= tableLength-1;i++){
        this.newTable.push(
            {sheet1:"调度依据",sheet2:this.tableData[i].num},
            {sheet1:this.tableData[i].region+'------'+this.tableData[i].Hname+"("+this.tableData[i].splitterRate+")",sheet2:''},
            {sheet1:"光路名称",sheet2:"GEPON工程(FTTH)("+this.tableData[i].num+")"+this.tableData[i].region+"/"+this.tableData[i].Hname+"001(1芯)"},
            {sheet1:'光路路由',sheet2:this.tableData[i].lightPath},
            {sheet1:this.tableData[i].region+"("+this.tableData[i].olt+")"+"------"+this.tableData[i].splitter+" 互联",sheet2:''},
            {sheet1:"联调分局",sheet2:this.tableData[i].region.substring(0,2)},
            {sheet1:"监理公司联系人",sheet2:this.tableData[i].supervision},
            {sheet1:"光缆施工联系人",sheet2:this.tableData[i].construction}
        )

        this.mergesData.push(
            `1-${2+i*8}:2-${2+i*8}`,
            `1-${5+i*8}:2-${5+i*8}`
        )

        this.cellStyleData.push(
            {
              cell:`1-${1+i*8}`,
              fill:{
                fgColor:{ rgb:"ffff33"}
              },
              font:{
                size:16
              }
            },
            {
              cell:`2-${1+i*8}`,
              fill:{
                fgColor:{ rgb:"ffff33"}
              },
              font:{
                size:16
              }
            }
        )

      }

      this.newTable.push(
          {sheet1:"调单联系人",sheet2:this.dispatchMan+this.phone}
      )

      this.$excel({
        filename:this.labelNum,
        sheet:[
          {
            sheetName: this.labelNum,
            keys: ["sheet1", "sheet2"],

            table:this.newTable,

            merges:this.mergesData,

            colWidth: [
                30,100
            ],

            globalStyle:{
              font:{
                name:"黑体"
              },
              alignment:{
                wrapText:true
              }
            },

            cellStyle:this.cellStyleData
          }
        ]
      })
    }
  }
}
</script>

