<template>
  <div>
    <el-form :inline="true">
        <div v-if="others">
            <el-form-item label="客户名称" class="others">
                <el-select v-model="name" placeholder="请选择" @change="selectWay" filterable>
                <el-option
                v-for="item in centerInfo"
                :key="item.value"
                :label="item.label"
                :value="item.value">
                </el-option>
                </el-select>
            </el-form-item>
        </div>
        <div v-else class="others">
            <el-form-item label="客户名称">
                <el-input v-model="name" placeholder="客户名称" clearable></el-input>
            </el-form-item>
        </div>

        <el-form-item label="中心点(A端)地址">
            <el-input v-model="Asite" placeholder="A端地址" clearable></el-input>
        </el-form-item>

        <el-form-item label="客户经理">
            <el-input v-model="manager" placeholder="客户经理" clearable></el-input>
        </el-form-item>

        <el-form-item label="调单编号">
            <el-input v-model="labelNum" placeholder="调单编号" clearable></el-input>
        </el-form-item>
        <el-button type="primary" @click="generateNum">生成调单号</el-button>
    </el-form>

    <el-table
      :data="tableData"
      style="width: 100%">
      <el-table-column
        prop="number"
        label="接入号"
        width="150">
      </el-table-column>
      <el-table-column
        prop="speed"
        label="带宽速率"
        width="100">
      </el-table-column>
      <el-table-column
        prop="eName"
        label="电路名称"
        width="180">
      </el-table-column>
      <el-table-column
        prop="eVlan"
        label="电路别名"
        width="100">
      </el-table-column>
      <el-table-column
        prop="Zpart"
        label="分支点对应运营局"
        width="150">
      </el-table-column>
      <el-table-column
        prop="ifRemove"
        label="是否拆除分支点光缆设备"
        width="180">
      </el-table-column>
      <el-table-column
        prop="lightNum"
        label="光缆编号"
        width="180">
      </el-table-column>
      <el-table-column
        prop="Zsite"
        label="分支点地址">
      </el-table-column>
    </el-table>

    <el-form :inline="true">
        <el-form-item label='接入号'>
            <el-input v-model="number"  clearable></el-input>
        </el-form-item>
        <el-form-item label='带宽速率'>
            <el-input v-model="speed"  clearable></el-input>
        </el-form-item>
        <el-form-item label='电路名称'>
            <el-input v-model="eName"  clearable></el-input>
        </el-form-item>
        <el-form-item label='电路别名'>
            <el-input v-model="eVlan"  clearable></el-input>
        </el-form-item>
        <el-form-item label='分支点对应运营局'>
            <el-input v-model="Zpart"  clearable></el-input>
        </el-form-item>
        <el-form-item label="是否拆除">
            <el-select v-model="ifRemoveValue" placeholder="请选择">
                <el-option
                v-for="item in ifRemove"
                :key="item.value"
                :label="item.label"
                :value="item.label">
                </el-option>
            </el-select>
        </el-form-item>
        <el-form-item label='拆机联系人'>
            <el-input v-model="contactMan"  clearable></el-input>
        </el-form-item>
        <el-form-item label='拆除光缆编号'>
            <el-input v-model="lightNum"  clearable></el-input>
        </el-form-item>
        <el-form-item label='分支点地址'>
            <el-input v-model="Zsite"  clearable></el-input>
        </el-form-item>
        <el-button type="primary" @click="addList">添加</el-button>
        <el-button type="success" round @click="downBtn">导出</el-button>
    </el-form>


  </div>
</template>

<script>
export default {
    created(){
        this.$axios.post("http://localhost:8888/centerInfoSelect").then(
            res=>{
                this.centerInfo = res.data.map(item=>{
                    return {value:item.id,label:item.username, site:item.site, regionPart:item.regionPart, manager:item.manager}
                })
                const otherData = {value:this.centerInfo.length+1, label:"其他"}
                this.centerInfo = [...this.centerInfo,otherData]            
            }
        ).catch(err=>console.log(err))

        this.$axios.post("http://localhost:8888/users",{
        username:this.$storeNew.get("username"),
        password:this.$storeNew.get("password")
      }).then((res)=>{
        this.dispatchNum = res.data[0][3]
        this.dispatchMan = res.data[0][4]
        this.phone = res.data[0][5]
      }).catch((err)=>console.log(err))

    },

    data(){
        return{
            others:true,
            name:'',
            centerInfo:[],
            dispatchNum:'',
            dispatchMan:'',
            phone:'',
            manager:'',
            Asite:'',

            number:'',
            speed:'',
            eName:'',
            eVlan:'',
            Zpart:'',
            ifRemove:[
                {value:1,label:"是"},
                {value:2,label:"否"}
            ],
            ifRemoveValue:'是',
            lightNum:'',
            Zsite:'',
            contactMan:'',

            tableData: []
        }
    },

    computed:{
        labelNum(){
            const d = new Date()
            return 'ZYBD'+d.getFullYear()+'-'+(d.getMonth()+1)+'-'+this.dispatchNum            
        } 
    },



    methods:{
        selectWay(index){
            if(index !== this.centerInfo.length){
                const newArr = this.centerInfo.filter(item=>index == item.value)
                this.Asite = newArr[0].site
                this.manager = newArr[0].manager
            }else{
                this.name = ''
                this.Asite=''
                this.manager=''
                this.others = !this.others
            }
        },

        generateNum(){
            this.dispatchNum++
            this.$axios.post('http://localhost:8888/update',
            {
                dispatchNum:this.dispatchNum,
                username:this.$storeNew.get("username")
            }
            ).then((res)=>console.log(res.data)).catch((err)=>console.log(err))
      },

        addList(){
            this.tableData.push({
                number:this.number,
                speed:this.speed,
                eName:this.eName,
                eVlan:this.eVlan,
                Zpart:this.Zpart,
                ifRemove:this.ifRemoveValue,
                lightNum:this.lightNum,
                Zsite:this.Zsite,
                contactMan:this.contactMan
            })

            this.number = '',
            this.speed = '',
            this.eName = '',
            this.eVlan = '',
            this.Zpart = '',
            this.ifRemoveValue = '',
            this.lightNum = '',
            this.Zsite = '',
            this.contactMan = ''
        },

        downBtn(){

            this.$excel({
                filename:this.labelNum,
                sheet:[
                    //表1信息
                    {
                        sheetName:this.labelNum,
                        keys:['col1','col2'],
                        table:[
                            {col1:"电路（线路）调度、局数据修改通知单（机密）",col2:""},
                            {col1:'业务类型',col2:"拆机"},
                            {col1:"中心点地址",col2:this.Asite},
                            {col1:"分支点地址",col2:"详见附表"},
                            {col1:"拆机联系人",col2:"详见附表"},
                            {col1:"客户经理",col2:this.manager},
                            {col1:"调单联系人",col2:this.dispatchMan+this.phone},
                        ],
                        colWidth:[40,40],
                        merges:['1-1:2-1'],
                        globalStyle:{
                            alignment:{
                                wrapText:true
                            },
                            font:{
                                sz:14
                            }
                        },
                        cellStyle:[
                            {
                                cell:'1-1',
                                fill:{
                                    fgColor:{rgb:'ffff33'}
                                },
                                font:{
                                   sz:16
                                }
                            }
                        ]
                    },

                    //表2信息
                    {
                        sheetName:'附表',
                        title:'业务信息',
                        tHeader:['接入号','速率','电路名称','电路别名','对应分局','是否拆除分支点设备','拆除光缆编号','分支点地址','拆机联系人'],
                        keys:['number','speed','eName','eVlan','Zpart','ifRemove','lightNum','Zsite','contactMan'],
                        table:this.tableData,
                        colWidth:[15,10,40,10,10,20,15,30,20],
                        globalStyle:{
                            font:{
                                sz:9
                            }
                        },
                        cellStyle:[
                            {
                                cell:'1-1',
                                fill:{
                                    fgColor:{rgb:"ffff33"}
                                },
                                font:{
                                    sz:14,
                                    bold:true
                                }
                            }
                        ]
                    }
                ]
            })
        }
    }
}
</script>

<style>
    .others{
        padding-bottom: 40px;
    }
</style>