<template>
<div id="login-container">
    <div id="bigBox">
        <h1>调单生成器</h1>
        <div class="inputBox">
            <div class="inputText">
                <i class="fas fa-user"></i>
                <input type="text" placeholder="账户" v-model="username">
            </div>
            <div class="inputText">
                <i class="fas fa-lock"></i>
                <input type="password" placeholder="密码" v-model="password">
            </div>
            <input type="button" value="登录" class="inputButton" @click="moveToHome">

        </div>
    </div>
</div>
</template>

<script>
export default {
    data(){
        return{
            username:'',
            password:'',
            dispatchNum:null
        }
    },


    methods:{
        moveToHome(){
            this.$axios.post("http://localhost:8888/users",
            {
               username:this.username,
               password:this.password
            }
            ).then((res)=>{
                if(res.data){
                    console.log(res.data[0])
                    this.dispatchNum = res.data[0][3]
                    this.$storeNew.set('username',this.username)
                    this.$storeNew.set('password',this.password)
                    this.$storeNew.set('dispatchNum',this.dispatchNum)
                    this.$router.push("/home").catch((err)=>console.log(err))
                }
            }).catch(
                (err)=>{
                    this.$message({
                        showClose: true,
                        message: '账户或密码错误!!!!',
                        type: 'error',
                        center:true,
                        offset:150
                    })
                    console.log(err)
                }
            )
        }
    }
}
</script>

<style>
*{
    margin:0;
    padding: 0;
}

#login-container{
    width: 100%;
    height: 100%;
    background: linear-gradient(to right, #fbc2eb, #a6c1ee);
    position: absolute;
}


#bigBox{
    margin: auto;
    margin-top: 200px;
    padding: 30px;
    width: 500px;
    height: 250px;
    border-radius: 20px;
    background-color: rgba(52, 52, 59, 0.64);
    text-align: center;
}

#bigBox h1{
    color: rgba(136, 206, 235, 0.84);
}

#bigBox .inputBox{
    margin-top: 40px;
}

#bigBox .inputBox .inputText{
    margin-top: 20px;
}

#bigBox .inputBox .inputText input{
    border: 0px;
    padding: 10px 10px;
    border-bottom: 1px solid white;
    background-color: rgba(52, 52, 59, 0.64);
    color: white;
}

#bigBox .inputBox .inputButton{
    width: 150px;
    height: 40px;
    margin-top: 30px;
    border-radius: 20px;
    font-size: 15px;
    background-color: #00DBDE;
    background-image: linear-gradient(90deg, #00DBDE 0%, #FC00FF 100%);
}

.fas{
    color: white;
    margin-right: 10px;
}
</style>